<template>
  <div class="contain">
    <van-loading
      class="loading"
      text-color="#999"
      v-if="playList == ''"
      color="red"
      type="spinner"
      size="24px"
      >正在加载...</van-loading
    >
    <van-list
      v-if="playList != ''"
      :immediate-check="false"
      v-model="loading"
      :finished="finished"
      finished-text="查看更多请前往网易云APP~"
      @load="onLoad"
    >
      <div class="content">
        <div
          @click="toPlayListDetail(item.id)"
          class="item"
          v-for="(item, index) in playList"
          :key="index"
        >
          <img :src="item.coverImgUrl+'?param=150y150'" />
          <p>{{ item.name }}</p>
          <img class="logo" src="../../assets/image/default.png" />
          <span class="iconfont icon-caret-right">{{ item.playCount }}</span>
        </div>
        <router-view></router-view>
      </div>
    </van-list>
  </div>
</template>

<script>
import { getPlayList } from "../../NetRequest/more";
import { toStringNum } from "../../common/common.js";
export default {
  data() {
    return {
      playList: [],
      offset: 0,
      playlists: 10,
      loading: false,
      finished: false,
    };
  },
  mounted() {
    this.myGetPlayList();
  },
  methods: {
    async myGetPlayList() {
      if (this.playlists < 10) {
        this.finished = true;
        return;
      }
      const res = await getPlayList(this.cat, 15, this.offset);
        // console.log(res);
      this.playlists = res.data.playlists.length;
      for (const item of res.data.playlists) {
        this.playList.push({
          id: item.id,
          name: item.name,
          coverImgUrl: item.coverImgUrl,
          playCount: toStringNum(item.playCount),
        });
      }
      //   console.log(this.playList);
      this.loading = false;
      this.offset += 15;
    },
    onLoad() {
      if (++this.onLoadCount >= 9) {
        this.finished = true;
        return;
      }
      setTimeout(() => {
        this.myGetPlayList();
      }, 2000);
    },
    /* 歌单详情 */
    toPlayListDetail(id) {
      // console.log(id);
      this.$router.push({
        path: "/recommendMore/playListDetail",
        query: {
          id,
        },
      });
    },
  },
  props: {
    cat: {
      type: String,
    },
  },
};
</script>

<style scoped>
.contain {
  margin-bottom: 50px;
}
.content {
  padding: 15px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.loading {
  margin-top: 20px;
  margin-left: 40%;
}
.item {
  width: 100px;
  height: 120px;
  position: relative;
  margin-bottom: 30px;
}
.item img {
  width: 100px;
  height: 100px;
  border-radius: 15px;
}
p {
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  font-size: 13px;
}
.item .iconfont {
  position: absolute;
  right: 5px;
  top: 5px;
  width: 50px;
  height: 20px;
  line-height: 20px;
  text-align: center;
  font-size: 10px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.3);
  /* background-color: #000; */
  filter: Alpha(opacity=50);
  border-radius: 10px;
  z-index: 10;
}
.item .logo {
  position: absolute;
  left: 5px;
  top: 5px;
  width: 20px;
  height: 20px;
  background-color: rgba(0, 0, 0, 0.3);
  -webkit-filter: contrast(200%); /* Chrome, Safari, Opera */
  filter: contrast(200%);
}
</style>