<template>
  <div class="newSongs">
    <div class="header">
      <i @click="backIndex" class="iconfont icon-arrowLeft"></i>
      <span class="text">歌单广场</span>
    </div>
    <div class="listIcon">
        <van-icon name="apps-o" />
    </div>
    <div class="navtab">
      <van-tabs @click="handleChange"  v-model="active" sticky offset-top="50">
        <van-tab title="推荐" name=""
          ><publicComps :cat="active"
        /></van-tab>
        <van-tab title="经典" name="经典"
          ><publicComps :cat="active"
        /></van-tab>
        <van-tab title="华语" name="华语"
          ><publicComps :cat="active"
        /></van-tab>
        <van-tab title="流行" name="流行"
          ><publicComps :cat="active"
        /></van-tab>
        <van-tab title="轻音乐" name="轻音乐"
          ><publicComps :cat="active"
        /></van-tab>
        <van-tab title="古风" name="古风"
          ><publicComps :cat="active"
        /></van-tab>
        <van-tab title="民谣" name="民谣"
          ><publicComps :cat="active"
        /></van-tab>
        <van-tab title="摇滚" name="摇滚"
          ><publicComps :cat="active"
        /></van-tab>
        <van-tab title="说唱" name="说唱"
          ><publicComps :cat="active"
        /></van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import publicComps from "./publicComps";
export default {
  components: {
    publicComps,
  },
  data() {
    return {
      active: "",
    };
  },
  mounted() {
    this.handelActive();
  },
  methods: {
    /* 返回首页 */
    backIndex() {
      this.$router.go(-1);
      sessionStorage.clear('activePage')
    },
    /* 解决刷新页面tabbar激活与实际页面不符问题 */
    handleChange() {
      // this.$router.push('/'+name)
      // this.$bus.$emit("handleChange", name);
      // console.log(this.active);
      sessionStorage.setItem("activePage", this.active)
    },
    handelActive() {
      if (sessionStorage.getItem("activePage")) {
        this.active = sessionStorage.getItem("activePage", this.active);
      } else {
        this.active = "";
      }
    },
  },
};
</script>

<style scoped>
.header {
  position: fixed;
  width: 100%;
  height: 20px;
  padding: 15px;
  line-height: 30px;
  text-align: left;
  background-color: #fff;
  display: flex;
  justify-content: left;
}
.icon-arrowLeft {
  margin-right: 20px;
  font-size: 25px;
}
.listIcon{
    position: fixed;
    right: 0;
    top: 60px;
    width: 50px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    background-color: #fff;
    font-size: 20px;
    border: 1px solid #fff;
    box-shadow:-3px 0px 5px -3px #999;
}
.navtab {
  position: absolute;
  width: 100%;
  top: 50px;
  z-index: -1;
}
</style>